.nccnits-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem 4rem;

}

.title{
    font-size: 1.4rem;
    font-weight: 600;
}

.title-underline{
    display: block;
    width: 100px;
    height: 2px;
    background-color: black;
    margin-bottom: 2rem;
}

.council{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90%;
    gap: 2rem;
    margin-bottom: 3rem;
}

.box{
    width: 33%;
    padding: 1rem 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 4px solid #404e67;
    border-radius: 0.5rem;
}

.content-title{
    font-size: 1.2rem;
    color: #404e67;
    font-weight: 500;
}
.content{
    font-size: 1rem;
    font-weight: 400;
}

.hr{
    width: 100%;
    background-color: #404e67;
    display: block;
    height: 3px;
    margin-bottom: 3rem;
}

.selection-bar{
    width: 90%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 3rem;
}

.tab{
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    color: #404e67;
}

.active-tab{
    background-color: #404e67;
    color: white;
    border-radius: 2rem;
    padding: 0.4rem 1rem;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
}

/* media-query */

@media screen and (max-width:540px) {
    .council{
        width: 100%;
        gap: 1rem;
    }
    .nccnits-container{
        margin: 1rem 0;
    }
    .content-title{
        font-size: 1rem;
    }
    .content{
        font-size: 0.8rem;
    }
    .box{
        width: 80%;
        padding: 1rem 2rem;
    }
    .tab{
        font-size: 0.8rem;
        text-align: center;
    }
    .active-tab{
        text-align: center;
        background-color: #404e67;
        color: white;
        border-radius: 1rem;
        padding: 0.2rem;
        font-size: 0.8rem;
        font-weight: 500;
        cursor: pointer;
    }
}

@media (min-width:540px) and (max-width:1024px) {
    .nccnits-container{
        margin: 1rem 0;
    }
    .content-title{
        font-size: 1rem;
    }
    .content{
        font-size: 0.8rem;
    }
    .box{
        width: 40%;
    }
    .tab{
        font-size: 0.8rem;
        text-align: center;
    }
}
