.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0rem 0rem;
  }
  
  .carousel {
    height: 70vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    /* margin-top: 2rem; */
    /* background-color: #f2f4f5; */
  }
  
 
  .see-more {
    background-color: #02044f;
    padding: 0.4rem 1rem;
    border-radius: 1rem;
    z-index: 3;
  }
  .see-more-text {
    color: white;
    font-size: 1rem;
    font-weight: 500;
  }
  
  .see-more:hover {
    transform: scale(1.2);
    transition: all 0.6s ease;
  }
  .text_carousel{
    display: flex;
    border: 20px solid red;

  }
  .quote {
    display: flex;
    text-align: center;
    font-size: 1.2rem;
    font-weight: 400;
    margin: 1rem 2rem;
  }
  
  .hr {
    display: flex;
    margin-left: 20%;
    height: 3px;
    width: 60%;
    background-color: black;
    align-items: center;
  }
  
  .message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 2rem;
    margin: 1rem 2rem;
  }
  
  .message-text {
    text-align: center;
    font-size: 1rem;
    font-weight: 500;
  }
  
  .message-author {
    font-size: 0.9rem;
    font-weight: 600;
  }
  
  .parent-section {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    padding: 0 2rem;
    margin-bottom: 2rem;
  }
  
  .title {
    font-size: 1.4rem;
    font-weight: 600;
  }
  .title-underline {
    display: block;
    width: 100px;
    height: 2px;
    background-color: black;
    margin-bottom: 2rem;
  }
  
  /* Media-Queries ->>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
  
  @media screen and (max-width: 320px) {
    .title {
      font-size: 1.2rem;
    }
    .home {
      padding: 0rem;
    }
    .quote {
      font-size: 1rem;
      margin: 1rem 0rem;
    }
    .message {
      padding: 1rem;
      margin: 1rem 0rem;
      align-items: center;
    }
  
    .message-text {
      font-size: 0.8rem;
    }
  
    .message-author {
      font-size: 0.7rem;
    }
  
    .parent-section {
      padding: 0 0.2rem;
      margin-bottom: 1rem;
    }
  }
  
  @media (min-width: 321px) and (max-width: 540px) {
    .carousel {
      align-items: flex-start;
    }
    .title {
      font-size: 1.2rem;
    }
    .home {
      padding: 0rem;
    }
  
    .quote {
      font-size: 1rem;
      margin: 1rem 0;
      padding: 0 1rem;
    }
  
    .message {
      padding: 1rem;
      margin: 1rem 0rem;
      align-items: center;
    }
  
    .message-text {
      font-size: 0.8rem;
    }
  
    .message-author {
      font-size: 0.7rem;
    }
  
    .parent-section {
      padding: 0 0.2rem;
      margin-bottom: 1rem;
    }
  
    .see-more {
      font-size: 0.8rem;
      padding: 0.2rem 0.6rem;
    }
  }
  
  @media (min-width: 540px) and (max-width: 768px) {
    .carousel {
      align-items: flex-start;
    }
    .home {
      width: 100%;
      padding: 0rem rem;
    }
  
    .quote {
      margin: 1rem 0;
      padding: 0 1rem;
    }
  
    .message {
      padding: 1rem;
      margin: 1rem 0rem;
      align-items: center;
    }
  }
  
  @media screen and (max-width: 1200px) {
    .carousel {
      height: 50vh;
    }
    .gallery-container {
      width: 95%;
    }
  }
  