.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #404e67;
    padding: 0.2rem 2rem;
    height: 80px;
}

.logo-container{
    display: flex;
    align-items: center;
}

.logo{
    width: 50px;
    height: 50px;
}

.title{
    color: white;
    margin-left: 1rem;
    font-size: 1.2rem;
    font-weight: 700;
}

.links{
    display: flex;
    width: 30%;
    justify-content:space-evenly;
    align-items: flex-end;
}

.link-tab{
    color: white;
    font-size: 1rem;
    font-weight: 500;
    padding-bottom: 0.6rem;
    transition: 0.5s ease;
}

.link-tab:hover{
    border-bottom: 2px solid white;
}

.active-link-tab{
    color: white;
    font-size: 1rem;
    font-weight: 500;
    padding-bottom: 0.2rem;
    border-bottom: 2px solid white;
}

.hamburger{
    display: none;
}

.hamburger>span{
    display: block;
    width: 20px;
    height: 2px;
    margin-top: 4px;
    background-color: white;
}

a{
    text-decoration: none;
}

@media screen and (max-width:540px) {
    .links{
        display: none;
    }
    .hamburger{
        display: block;
    }
}

@media (min-width:540px)and (max-width:768px) {
    .links{
        width: 100%;
        gap: 0.2rem;
    }
    .link-tab{
        font-size: 0.7rem;
    }
}

@media (min-width:768px)and (max-width:1024px) {
    .links{
        width: 50%;
        gap:0.8rem;
    }
}